@import url(https://fonts.googleapis.com/css?family=Open+Sans+Condensed:300|Special+Elite);
@import url(https://fonts.googleapis.com/css?family=Bonbon|Caveat);
@import url(https://fonts.googleapis.com/css?family=Raleway|Staatliches);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.bar-top {
  top: 0; }

.bar-bottom {
  position: absolute;
  bottom: 0; }

.bar-black {
  background-color: black;
  display: flex;
  align-items: center;
  justify-content: center; }

.projects-container {
  height: 100%;
  width: 65vw;
  margin: 0 auto;
  display: grid;
  grid-auto-flow: column;
  grid-gap: 1rem; }
  .projects-container-display {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.815);
    background-position: left;
    background-size: cover;
    background-repeat: no-repeat; }
    .projects-container-display:hover {
      opacity: 0.75; }

.project-slide {
  font-family: 'Staatliches', cursive;
  color: black;
  margin: 0 auto;
  width: 65vw;
  background-size: cover;
  background-position: center; }
  .project-slide-cover {
    opacity: 1;
    height: 100%;
    background-color: rgba(255, 255, 255, 0.8);
    overflow: auto;
    margin: 0 auto; }
    .project-slide-cover-title {
      padding-top: 5%;
      font-size: 2rem;
      text-align: middle; }
    .project-slide-cover-tech {
      padding-bottom: 2.5%; }
    .project-slide-cover-description {
      font-family: 'Raleway', sans-serif;
      font-size: 1.25;
      margin: 0 5% 2.5% 5%; }
    .project-slide-cover-link {
      text-decoration: none;
      margin: 0 2.5%;
      overflow: auto; }

.title {
  padding: 1vh;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content; }
  .title-title {
    margin-bottom: 0; }
  .title-description {
    font-family: 'Open Sans Condensed', sans-serif;
    margin: 0; }
  .title-links {
    text-decoration: none;
    font-family: 'Open Sans Condensed', sans-serif;
    margin: 0.5vw; }

.App {
  text-align: center;
  height: 100vh;
  display: grid;
  grid-template-rows: 10vh 80vh 10vh; }

.content {
  font-family: 'Special Elite', cursive;
  height: 80vh;
  display: flex;
  flex-flow: column; }

.carousel {
  padding: 1.5%;
  height: inherit;
  display: flex; }

